import { styled, Typography, Box } from "@mui/material";
import React from "react";

const MainHomeDiv = styled("div")(({ theme }) => ({
  padding: 0,
  position: "relative",
  height: "calc(150vh)",
  width: "100%",
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: 'url("//unsplash.it//1921/1080")',
  backgroundSize: "cover",
  "@media (max-width: 650px)": {
    height: "250vh",
  },
}));

const Footer = styled("div")(({ theme }) => ({
  padding: 0,
  marginTop: "400px",
  position: "absolute",
  bottom: 0,
  height: "80px",
  color: "white",
  width: "100%",
  display: "flex",
  flexDirection: "row",
  alignItems: "center",
  justifyContent: "center",
  backgroundColor: "black",
  backgroundSize: "cover",
  "@media (max-width: 650px)": {
    height: "150px",
  },
}));

const Home = () => {
  return (
    <MainHomeDiv>
      <Box sx={{ width: "40%" }}>
        <Typography
          variant="h3"
          sx={{
            textAlign: "center",
            "@media(max-width:650)": {
              fontSize: "10px",
            },
          }}
        >
          Welcome to the most relible source of servicemen
        </Typography>
        <Typography variant="body1">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Distinctio,
          veniam. Iusto repellat dolorum ea vero eaque! Dignissimos itaque
          maxime excepturi, ipsum quae quam?
        </Typography>
      </Box>
      <Box>
        <Typography
          variant="h3"
          sx={{
            marginTop: "300px",
            "@media(max-width:650)": {
              fontSize: "20px",
            },
          }}
        >
          Try a new cheaper experience
        </Typography>
        <Typography variant="body1">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Distinctio,
          veniam. Iusto repellat dolorum ea vero eaque! Dignissimos itaque
          maxime excepturi, ipsum quae quam?
        </Typography>
      </Box>
      <Footer>Tonysilversmith. All rights reserved. &copy; </Footer>
    </MainHomeDiv>
  );
};

export default Home;
