import React from "react";
import { Box, Typography } from "@mui/material";

const About = () => {
  return (
    <Box
      sx={{
        m: 10,
        height: "calc(100vh - 100px)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        "@media(max-width:650px)": {
          flexDirection: "column",
        },
      }}
    >
      <Box
        sx={{
          minHeight: "45vh",
          minWidth: "45vh",
          borderRadius: "50%",
          backgroundImage: 'url("//unsplash.it/400/400")',
          m: "0px 250px 0px 0px",
          "@media(max-width:650px)": {
            m: 10,
          },
        }}
      ></Box>
      <Box>
        <Typography variant="h3">About Us</Typography>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Rem aliquam
        earum optio. Officia cupiditate harum perferendis ea modi aperiam, odio
        deserunt voluptatem labore facilis quia! Sapiente totam perferendis
        incidunt nostrum nobis ipsa excepturi iusto iure ullam nisi
        exercitationem, et ducimus.
      </Box>
    </Box>
  );
};

export default About;
