import React from "react";
import Card from "@mui/material/Card";
import CardActions from "@mui/material/CardActions";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";

function MediaCard(props) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  return (
    <>
      <Modal
        aria-labelledby="Service-Card"
        aria-describedby="Service-Card"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 1500,
        }}
      >
        <Fade in={open}>
          <Box
            sx={{
              position: "absolute",
              height: 500,
              width: 500,
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              bgcolor: "background.paper",
              border: "1px solid #777",
              borderRadius: 2,
              boxShadow: 24,
              p: 4,
              "@media(max-width: 650px)": {
                height: 450,
                width: 250,
              },
            }}
          >
            <CardMedia
              component="img"
              height="140"
              image={`//unsplash.it/${props.height}/${props.width}`}
              alt="IMAGE"
              sx={{ p: "20px auto" }}
            />
            <Box
              sx={{
                p: "22% 0px 22% 0px",
                "@media(max-width: 650)": {
                  p: "50% 0px 22% 0px",
                },
              }}
            >
              <Typography id="Service-Card" variant="h6" component="h2">
                {props.name}
              </Typography>
              <Typography id="Service-Card" sx={{ mt: 2 }}>
                {props.title}
              </Typography>
            </Box>
            <Button variant="contained" size="large">
              Book Now
            </Button>
          </Box>
        </Fade>
      </Modal>
      <Card sx={{ maxWidth: 345, m: 5 }}>
        <CardMedia
          component="img"
          height="140"
          image={`//unsplash.it/${props.height}/${props.width}`}
          alt="IMAGE"
        />
        <CardContent>
          <Typography gutterBottom variant="h5" component="div">
            {props.name}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            {props.title}
          </Typography>
        </CardContent>
        <CardActions>
          <Button
            size="small"
            onClick={() => {
              handleOpen();
            }}
          >
            Have a Try
          </Button>
          <Button
            size="small"
            onClick={() => {
              handleOpen();
            }}
          >
            Book Person
          </Button>
        </CardActions>
      </Card>
    </>
  );
}

let data;

fetch("https://jsonplaceholder.typicode.com/users")
  .then((response) => response.json())
  .then((json) => {
    data = json;
  });

const Services = () => {
  return (
    <>
      <Typography variant="h4">Contact our best servicemen</Typography>
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "repeat(4, 1fr)",
          alignItems: "center",
          justifyContent: "center",
          gap: "1 rem",
          "@media(max-width: 650px)": {
            display: "flex",
            flexDirection: "column",
          },
        }}
      >
        {data.map((element) => {
          return (
            <MediaCard
              key={element.id}
              name={element.name}
              title={element.company.catchPhrase}
              height={Math.floor(Math.random() * 1000 + 400)}
              width={Math.floor(Math.random() * 1000 + 400)}
            />
          );
        })}
      </Box>
    </>
  );
};

export default Services;
